import React from 'react';
import {Box} from "@mui/material";
import {DataGrid, GridToolbar} from "@mui/x-data-grid";
import {apiStore} from "../../stores/AppStore";
import {observer} from "mobx-react-lite";

function Grid() {
    const columns = [
        {field: 'family', headerName: '', width: 90},
        {
            field: 'name',
            headerName: '',
            width: 150,
        },
        {
            field: 'surname',
            headerName: '',
            width: 150,
        },
        {
            field: 'sex',
            headerName: '',
            width: 150,
        },
        {
            field: 'country',
            headerName: '',
            width: 150,
            valueGetter: (params) => {
                return params.label;
            }
        },
        {
            field: 'age',
            headerName: '',
            width: 150,
        },
        {
            field: 'food',
            headerName: '',
            width: 400,
            valueGetter: (params) => {
                let result = ''
                if (params?.dietician) result += '\n'
                if (params?.vegan) result += '\n'
                if (params?.traditional) result += '\n'
                return result
            }
        },
    ]

    return (
        <div className={'main_panel'}>
            <Box sx={{ width: '100%'}}>
                <DataGrid
                    slots={apiStore.hasFilters ? {toolbar: GridToolbar} : {}}
                    rows={apiStore.text}
                    columns={columns}
                    initialState={{
                        pagination: {
                            paginationModel: {
                                pageSize: 10,
                            },
                        },
                    }}
                    pageSizeOptions={[5]}
                    checkboxSelection
                    disableRowSelectionOnClick
                />
            </Box>

        </div>
    );
}

export default observer(Grid);